import React from 'react';
import styled, { css } from 'styled-components/macro';
import { menuData } from '../data/menuData';
import { NavHashLink as Link } from 'react-router-hash-link';
import { RiMenu4Fill } from 'react-icons/ri';
import { GiRockingChair } from 'react-icons/gi'



const Nav = styled.nav`
height: 60px;
display: flex;
background: transparent;
padding:1rem 2rem;
justify-content: space-between;
z-index: 10;
width: 100%;
position:fixed;
`;
const  NavBold= styled.nav`
height: 60px;
display: flex;
background: #f5f5f5;
padding:1rem 2rem;
justify-content: space-between;
z-index: 10;
width: 100%;
position:fixed;

`;


const NavLink = css`
color:black;
display: flex;
align-items: center;
padding:0 1rem;
height:100%;
font-size:1.5rem;
cursor:pointer;
text-decoration: none;
&:hover{
    color: #555D50;
}
`;


const Logo = styled(Link)`
${NavLink}
font-style: italic;
`;


const NavMenu = styled.i`
display:flex;
align-items:center;
margin-right:10px;
@media screen and (max-width: 768px){
display:none;
}
font-size: 1.2rem
`;
const NavMenuLinks = styled(Link)`
${NavLink};
&:hover {
    transform: translateY(-2px);
    color:#313639;
}

`;


const LogoIcon = styled(GiRockingChair)`
font-size:2.5rem;
color:black;
font-weight:700;
&:hover{
    color: #777;
}
`;

const MenuBars = styled(RiMenu4Fill)`
color: black;
display:none;
@media screen and (max-width: 768px){
    display: block;
    font-size:2rem;
    margin-right: 10px;
&:hover{
    color: #555D50;
}
}
`;



const Navbar = function ({ toggle }) {
const [navbar, setNavbar] = React.useState(false)
   const navbarScroll =()=>{
      if(window.scrollY <= 70){
          setNavbar(false)
      } else{
          setNavbar(true)
      }
   }

   window.addEventListener('scroll', navbarScroll)

    return (
        <>
           {navbar ? ( <NavBold>
                <Logo to='#home'><LogoIcon></LogoIcon></Logo>
                <MenuBars onClick={toggle} />
                <NavMenu>
                    {menuData.map((data, index) => (
                        <NavMenuLinks to={data.link} key={index} onClick={toggle}>
                            {data.title}
                        </NavMenuLinks>
                    ))}
                </NavMenu>
            </NavBold>) :
            (
                <Nav>
                <Logo to='#home'><LogoIcon></LogoIcon></Logo>
                <MenuBars onClick={toggle} />
                <NavMenu>
                    {menuData.map((data, index) => (
                        <NavMenuLinks to={data.link} key={index} onClick={toggle}>
                            {data.title}
                        </NavMenuLinks>
                    ))}
                </NavMenu>
            </Nav>
            )}

        </>
    )
}

export default Navbar
