import React, { useState, useRef, useEffect } from 'react';
import styled, { css } from 'styled-components/macro';
import { sliderData } from '../data/sliderData';
import { Button } from '../components/Button';
import { IoIosArrowBack, IoIosArrowForward } from 'react-icons/io';


const HomepageSection = styled.section`
height: 100vh;
/* max-height: 1100px; */
position: relative;
overflow:hidden;
@media screen and (max-width: 768px){
    height: 92vh;
}
`;



const HomePageWrapper = styled.div`
width: 100%;
height: 100%;
display: flex;
justify-content: center;
align-items: center;
overflow: hidden;
position: relative;

`;

const HomeSlide = styled.div`

width:100%;
height:100%;
`;
const HomeSlider = styled.div`
position:absolute;
top:0rem;
left:0rem;
height:100%;
width:100%;
display:flex;
align-items: center;
justify-content:center;
`;


const HomeImage = styled.img`
width: 100vw;
height: 100vh;
top:0;
left:0;
transition: 0.4s ease;
transform: 100% 50%;
position:absolute;
object-fit: cover;
text-align: right;
object-position: 35% 0%;
@media screen and (max-width: 768px){
object-position: 35% 0%;
}
`;



const HomeContent = styled.div`
position: absolute;
z-index: 5;
display: flex;
flex-direction: column;
max-width: 1600px;
width: calc(100%-100px);
color: black;
right: 4rem;
bottom: 15rem;
@media screen and (max-width: 768px){
bottom:10rem;
right:1.5rem;
}
`;





const SliderBtn = styled.div`
position:absolute;
bottom:50px;
right:50px;
display: flex;
z-index:4;
`;

const prevNextArrows = css`
width: 50px;
height: 50px;
color: black;
cursor: pointer;
padding: 5px;
margin-right: 10px;
user-select: none;
transition: 0.3s;
&:hover{
    color: #fff; 
    transform: scale(1.1)
}
`;


const Prev = styled(IoIosArrowBack)`
${prevNextArrows}
`;
const Next = styled(IoIosArrowForward)`
${prevNextArrows}
`;


const Homepage = () => {
    const [current, setCurrent] = useState(0);
    const picLength = sliderData.length;
    const timeout = useRef(null);
    



    

useEffect(() => {

         const nextSlide = () =>{
             setCurrent(current =>(current === picLength - 1 ? 0 : current +1))
         }
         timeout.current = setTimeout(nextSlide, 4000)
        return function(){
            if(timeout.current){
                clearTimeout(timeout.current);
            }
            
        };
    }, [current, picLength]);


 



    const nextSlide = () => {
        if(timeout.current){
            clearTimeout(timeout.current);
        }
        setCurrent(current === picLength - 1 ? 0 : current + 1)

    }
    const prevSlide = () => {

        setCurrent(current === 0 ? picLength - 1 : current - 1)
    }

    if(!Array.isArray(sliderData) || sliderData.length <=0){
        return null
    }
    
    return (
        <HomepageSection id='home'>
            <HomePageWrapper>
                {sliderData.map((slide, index) => (
                    <HomeSlide key={index}>

                        {index === current && (
                            <HomeSlider>
                            <HomeImage src={slide.image} alt={slide.name} />
                                <HomeContent>
                                    <h1>{slide.title}</h1>
                                    <Button to="#gallery" primary='true'
                                        css={`
                                    z-index: 1;
                                     `}
                                    >
                                        {slide.label}
                                    </Button>
                                </HomeContent>
                            </HomeSlider>
                        )}

                    </HomeSlide>
                ))}
                <SliderBtn>
                    <Prev onClick={prevSlide} />
                    <Next onClick={nextSlide} />
                </SliderBtn>
            </HomePageWrapper>
        </HomepageSection>
    )
}

export default Homepage
