import React from 'react';
import styled from 'styled-components';
import { Button } from '../components/Button';
import galleryData from '../data/GalleryData'
import {ImSpinner2} from 'react-icons/im';
import {keyframes} from 'styled-components'



const Section = styled.section`
width: 100%;
height: fit-content;
margin-bottom: 2rem;

`;

  
  



const Container = styled.div`
padding: 3rem 3rem;
display:grid;
grid-template-columns: 1fr 1fr;
grid-template-rows:600px;
@media screen and (max-width: 768px){
    grid-template-columns: 1fr;
    grid-template-rows:400px;
    padding: 3rem 0rem;
}
margin: 10px

`;


const ColumnLeft = styled.div`
display:flex;
width: 100%;
flex-direction:column;
justify-content: center;
align-items: flex-start;
line-height:1.8;
padding: 1rem 1rem;
order: ${({ reverse }) => (reverse ? '1' : '2')};

h1{
    margin-bottom: 0.5rem;
    margin-top: -5px;
}
h5{
    margin-bottom: 1rem;
    line-height: 1.2rem;
    text-align: left;
}
@media screen and (max-width: 768px){
    margin-top: 0rem;
    font-size: 1.4rem;
}
`;


const ColumnRight = styled.div`
display:flex;
justify-content: center;
align-items: center;
padding: 1rem 2rem;
order: ${({ reverse }) => (reverse ? '1' : '2')};
@media screen and (max-width: 768px){
    order: ${({ reverse }) => (reverse ? '2' : '1')};
    padding: 0rem 0rem;
    
}
`;






const Image = styled.img`
width:100% ;
height: 100%;
object-fit:cover;
padding: 1rem;
object-position: 10% 50%;
@media screen and (max-width: 768){
    order: ${({ reverse }) => (reverse ? '2' : '1')};
   
}
`;



const rotate = keyframes`
0% { transform: rotate(0deg); }
100% { transform: rotate(360deg); }
`
const Spin = styled(ImSpinner2)`
font-size:2rem;
 animation-name: ${rotate};
 animation-duration: 1s;
 animation-iteration-count: infinite;
`;



const perPage = 1;
const allData = galleryData;
const reducer = (state, action)=>{
switch(action.type){
   case "start":
       return {...state, loading:true}
       case "loaded":
           return{
               ...state,
               loading:false,
               data:[...state.data, ...action.newData],
               more: action.newData.length === perPage,
               after: state.after + action.newData.length 
           }
           default:
               throw new Error("Error")
}
};





function GalleryPage() {


  

    const [state, dispatch] = React.useReducer(reducer, {
        loading:false,
        more:true,
        data:[],
        after:0
    });

    const {data, loading, after, more} = state;

   
    const [element, setElement] = React.useState(null);


    React.useEffect(()=>{
        const currentElement = element;
        const currentObserver = observer.current;
        if(currentElement){
            currentObserver.observe(currentElement)
        }

        return()=>{
            if(currentElement){
                currentObserver.unobserve(currentElement)
            }
        }
    }, [element])
   
    const observer = React.useRef(new IntersectionObserver((entries)=>{
        const first = entries[0];
       if(first.isIntersecting){
           loader.current()
       }
    },{
        threshold: 0.2
    }))

    const load =  React.useCallback(()=>{
        dispatch({type: "start"});

        setTimeout(()=>{
            const  newData = allData.slice(after, after + perPage );
            dispatch({type: "loaded", newData})
        },1000)
    }, [after])
    const loader = React.useRef(load)
    React.useEffect(()=>{
        loader.current = load;
    }, [load])
    return (
         <Section id='gallery'>
                <>
                        {data.map(gallery => (
                            <div  key={gallery.id}>
                                 <Container >
                            <ColumnLeft className='left'>
                                <h1>{gallery.title}</h1>
                                <h5>{gallery.desc}</h5>
                                <Button to="quote">{gallery.view}</Button>
                                
                            </ColumnLeft>
                            <ColumnRight reverse={gallery.reverse} className='right'>

                                <Image src={gallery.image} alt={gallery.title + "image"} />
                            </ColumnRight>
                            
                            </Container>
                            </div>
                            
                            
                ))}
                {loading && <Spin></Spin>}
                {!loading && more && (<li ref={setElement}>
                    <div
                onClick={load}
                ></div></li>)}
                 
               
                 </>
            </Section>
            
        
    )
}

export default GalleryPage;
