import React from 'react';
import styled from 'styled-components';
import { menuData } from '../data/menuData';
import {NavHashLink as Links} from 'react-router-hash-link' 





const SidebarContainer = styled.div`
display: none;
position: fixed;
z-index: ${({open})=>(open ? '999' : '-10')};
width:50%;
height:92.5vh;
opacity:${({open})=>(open ? '1' : '0')};
background-color: white;
transition: 0.4s ease-ease-in-out;
position: absolute;
right:0;
top: 60px;
 @media screen and (max-width: 768px){
     width:100%;
     display: block;
     position:fixed;
     background-color:#f5f5f5;
}
`;



const SidebarWrapper = styled.div`
`;

const SidebarMenu = styled.div`
margin-top:2rem;
`;

const SideMenuLinks = styled.div`
    display:flex;
    justify-content:center;
    align-items:center;
    width: 100% 2rem;
    height: 5rem;font-size: 1.5rem;
    transition: 0.3s ease;
    &:hover{
        transform: scale(1.1);
       cursor: pointer;
       background-color: #777;
    }
`;




const  Sidebar= ({toggle,open}) =>{
    return (
        <SidebarContainer open={open} onClick={toggle}>
            <SidebarWrapper>
                <SidebarMenu>
                    {menuData.map((data, index) => (
                        <SideMenuLinks key={index}>
                            <Links to={data.link} className='links'>
                                {data.title}
                            </Links>
                        </SideMenuLinks>
                    ))}
                </SidebarMenu>
            </SidebarWrapper>
        </SidebarContainer>
    )
}

export default Sidebar;